import React, { useEffect, useState } from "react";
import twitterLogo from './assets/twitter-logo.svg';
import './App.css';

const TWITTER_LINK = `https://twitter.com/Rafael41603219`;

const TEST_GIFS = [
  "https://giphy.com/embed/r8b5bRsznrT6naOLq9",
  "https://giphy.com/embed/ERt4qEWqOC8fRTPcAw",
  "https://giphy.com/embed/5rqPkvN9pveHOuT3bx",
  "https://giphy.com/embed/l1J9xOZzfmKpYep3O",
  "https://giphy.com/embed/13u96fd8W8yCQw",
  "https://giphy.com/embed/ELham0Mveox9e",
  "https://giphy.com/embed/3oriOdiQDLHsNVHBAs",
]

const App = () => {

  const [walletAddress, setWalletAddress] = useState(null);

  const checkIfWalletIsConnected = async () => {
    try {
      const { solana } = window;

      if (solana) {
        if(solana.isPhantom) {
          console.log("Phantom wallet encontrada!! ğŸ¥³");
          const response = await solana.connect({onlyIfTrusted: true});
          console.log("Conectado con la llave pÃºblica: ", response.publicKey.tostring());

          setWalletAddress(response.publicKey.toString());

        }
      } else {
        alert("Wallet no encontrada ğŸ˜. Descarga phantom wallet ğŸ‘»");
      }
    } catch(error) {
      console.log(error);
    }
  };

  const connectWallet = async () => {
    const { solana } = window;

    if (solana) {
      const response = await solana.connect();
      console.log("Connected with Public Key: ", response.publicKey.toString());
      setWalletAddress(response.publicKey.toString());
    }
  };

  const renderNotConnectedContainer = () => (
    <button className="cta-button connect-wattet-button"
    onclick={connectWallet()}>
      Connect ğŸ”Œ
    </button>
  );

  const renderConnectedContainer = () => (
    <div className="connected-container">
      <div className="gif-grid">
        {TEST_GIFS.map(gif => (
          <div className="gif-item" key={gif}>
            <img src={gif} alt={gif} />
          </div>
        ))}
      </div>
    </div>
  )

  /** useEffect se llama una vez en el montaje del componente cuando
  * ese segundo parÃ¡metro (el []) esta vacÃ­o.
  */
  useEffect(() => {
    const onLoad = async () => {
      await checkIfWalletIsConnected();
    };
    window.addEventListener('load', onLoad);
    return () => window.removeEventListener('load', onLoad);
  }, []);

  return (
    <div className="App">
      {/* This was solely added for some styling fanciness */}
      <div className={walletAddress ? 'authed-container' : 'container'}>
        <div className="header-container">
          <p className="header">Cypherpunk Revolution ğŸ´â€â˜ ï¸</p>
          <p className="sub-text">
            View the cypherpunk messages...
          </p>
          {/* Render your connect to wallet button right here*/}
          {!walletAddress && renderNotConnectedContainer()}
          {/** We just need to add the inverse here!*/}
          {walletAddress && renderConnectedContainer()}
        </div>
        <div className="footer-container">
          <img alt="Twitter Logo" className="twitter-logo" src={twitterLogo} />
          <a
            className="footer-text"
            href={TWITTER_LINK}
            target="_blank"
            rel="noreferrer"
          >{`Built by rafaelfuentes.eth ğŸ˜`}</a>
        </div>
      </div>
    </div>
  );
};

export default App;
